<template>
  <div class="container">
    <div class="seachbox">   
        <input class="inputText fl" type="text" placeholder=" ISBN" v-model="bookISBN">      
        <input v-on:click="seachBook" class="seachBtn fl" type="button" value="">
    </div>

    <div>      
      <input v-on:click="miashiti" class="mianshiti" type="button" value="">      
    </div>

    <div class="book-msg">
      <h3 class="book-name">{{bookName}}</h3>
      <div class="book-base-msg">       
        <img class="bookimage col-md-4 fl" v-bind:src="imgUrl" alt="">        
        <div class="bookmsglist col-md-8 fl">
          <div>:<a href="">{{author}}</a></div>
          <div>:{{publisher}}</div>
          <div>:<a href="">{{publisher2}}</a></div>
          <div>:{{origin_title}}</div>
          <div>:<a href="">{{translator}}</a></div>
          <div>:{{pubdate}}</div>
          <div>:{{pages}}</div>
          <div>:{{price}}</div>
          <div>:{{binding}}</div>
          <div>ISBN:{{isbn}}</div>
        </div>
        
        <div>
          <h3 class="col-md-12 text-align-l margin-t10 itme-title"></h3>
          <div class="summary col-md-12 ">
            <p class="col-md-12 text-align-l">{{summary}}</p>
          </div>
          <h3 class="col-md-12 text-align-l margin-t10 itme-title"></h3>
          <div class="author_intro col-md-12 ">
            <p class="col-md-12 text-align-l">{{author_intro}}</p>
          </div>
          <h3 class="col-md-12 text-align-l margin-t10 itme-title"></h3>
          <div class="catalog col-md-12 ">
            <p class="col-md-12 text-align-l">{{catalog}}</p>
          </div>
      </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'bookMsg',
  /**---------------------------------------------------*/
  data () {
    return {
      bookName:'',
      bookISBN:'',
      imgUrl:'url',
      author:'',
      publisher:'',
      publisher2:'',
      origin_title:'',
      pubdate:'',
      translator:'',
      pages:'',
      price:'',
      isbn:'ISBN',
      binding:'',
      summary:'',
      author_intro:'',
      catalog:'',
      books:[]
    }
  },
  /**---------------------------------------------------*/
  /**-----https://api.douban.com/v2/book/isbn/9787544263160-------------*/
  created:function(){
    var _this = this;
    _this.isbn= _this.$route.params.isbn;//ISBN
    console.log(_this.isbn);
    this.$jsonp('https://api.douban.com/v2/book/isbn/'+_this.isbn)
        .then(function(info){
          _this.bookName = info.title;
          _this.author = info.author;
          _this.imgUrl = info.image;
          _this.publisher = info.publisher;
          _this.pubdate = info.pubdate;
          _this.translator = info.translator;
          _this.author = info.author;
          _this.pages = info.pages;
          _this.price = info.price;
          _this.binding = info.binding;
          _this.isbn = info.isbn13;
          _this.summary = info.summary;
          _this.author_intro = info.author_intro;
          _this.catalog = info.catalog;
          console.log(info);
        });   
  },
  methods:{
    /**--------------------isbn-------------------------------*/
    seachBook:function(){
      console.log('');
      var _this = this;
      this.$jsonp('https://api.douban.com/v2/book/isbn/'+_this.bookISBN)
          .then(function(info){
            console.log(info.title);
            console.log(_this.bookName2);
          _this.bookName = info.title;
          _this.author = info.author;
          _this.imgUrl = info.image;
          _this.publisher = info.publisher;
          _this.pubdate = info.pubdate;
          _this.translator = info.translator;
          _this.author = info.author;
          _this.pages = info.pages;
          _this.price = info.price;
          _this.binding = info.binding;
          _this.isbn = info.isbn13;
          _this.summary = info.summary;
          _this.author_intro = info.author_intro;
          _this.catalog = info.catalog;
        })
          .catch(function(err){
          console.log(err);
        });
    },
    miashiti:function(){
      this.$router.push({path:'/mianShiTi'});
    }
  }
}
// https://api.douban.com/v2/book/search?q='bookname'
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
  margin:0px;
  padding:0px;
}
a {
  color: #42b983;
}
.margin-t10{
  margin-top:10px
}
.fl{
  float: left;
}
.fr{
  float: right;
}

.seachbox{
  height: 40px;
  width: 100%;
}

.inputText{
  height: 40px;
  width:80%;
  border:1px solid #007fff;
  font-size: 18px;
}
.seachBtn{
  height: 40px;
  width: 60px;
  background: #007fff;
  color: #fff;
  font-size: 18px;
  border-radius: 3px;
}
.bookmsg{
  margin-top: 20px;
  padding-left: 5px;
  padding-right: 5px;
}
.bookmsglist{
  width: 70%;
  text-align: left;
  padding-left: 5px;
}
.book-name{
  text-align: left;
}
.text-align-l{
  text-align: left;
  
}
.itme-title{
  color: blue;
}
.bookimage{
  width: 30%;
  max-width: 200px;
  min-width: 50px;
  margin-top:28px;
}
.summary,.author_intro,.catalog{
  border:1px solid #ccc;
  border-radius:5px;
  background: #d9d9f3;
}
.mianshiti{
  height: 40px;
  width: 100%;
  background: #007fff;
  color: #fff;
  font-size: 18px;
  border-radius: 3px;
}
</style>



