<template>
	<div class="box">
		<el-form :model="ruleForm"  label-position="top" align="left" status-icon  :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="medium">
			<el-form-item label="" prop="account" placeholder=",6-16">
				<el-input v-model="ruleForm.account"></el-input>
			</el-form-item>
			<el-form-item label="" prop="chianise_name"  placeholder="">
				<el-input v-model="ruleForm.chianise_name"></el-input>
			</el-form-item>
			<el-form-item label="" prop="sex">
				<el-radio-group v-model="ruleForm.sex">
					<el-radio label=""></el-radio>
					<el-radio label=""></el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item label="" prop="age">				
				<el-col :span="20">
					<el-form-item prop="age">
						<el-date-picker type="date" placeholder="" v-model="ruleForm.age" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
			</el-form-item>
			<el-form-item label="" prop="marriage">
				<el-radio-group v-model="ruleForm.marriage">
					<el-radio label=""></el-radio>
					<el-radio label=""></el-radio>
					<el-radio label=""></el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item label="" prop="IDNumber">
				<el-input v-model="ruleForm.IDNumber" placeholder="18"></el-input>
			</el-form-item>
			<el-form-item label="" prop="phone">
				<el-input v-model="ruleForm.phone" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="email">
				<el-input v-model="ruleForm.email" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="pass">
				<el-input type="password" v-model="ruleForm.pass" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item label="" prop="checkPass">
				<el-input type="password" v-model="ruleForm.checkPass" auto-complete="off"></el-input>
			</el-form-item>			
			<el-form-item label="" prop="hobbies">
				<el-checkbox-group v-model="ruleForm.hobbies">
					<el-checkbox label="" name="hobbies"></el-checkbox>
					<el-checkbox label="" name="hobbies"></el-checkbox>
					<el-checkbox label="" name="hobbies"></el-checkbox>
					<el-checkbox label="" name="hobbies"></el-checkbox>
					<el-checkbox label="" name="hobbies"></el-checkbox>
					<el-checkbox label="" name="hobbies"></el-checkbox>
					<el-checkbox label="" name="hobbies"></el-checkbox>
				</el-checkbox-group>
			</el-form-item>
			<el-form-item label="" prop="job_experience">
				<el-input type="textarea" v-model="ruleForm.job_experience"  placeholder="300"></el-input>
			</el-form-item>
			<el-form-item label="" prop="intrduoce">
				<el-input type="textarea" v-model="ruleForm.intrduoce"  placeholder="300"></el-input>
			</el-form-item>
			<el-form-item label="" prop="open_resume">
    			<el-switch v-model="ruleForm.open_resume"></el-switch>
  			</el-form-item>

			<el-form-item>
				<el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
				<el-button @click="resetForm('ruleForm')"> </el-button>
			</el-form-item>
		</el-form>			
	</div>
</template>

<script>

	import axios from 'axios'
	export default {
		data() {
			//(_)6-16
			var validateAccount = function (rule, value, callback) {
				let reg_account= /^([a-zA-Z]{1}\w*){3,12}$/;
				if (value === '') {
					callback(new Error(''));
				}else if(!(reg_account.test(value))){
					callback(new Error('(_)3-12'));
				}else{
					callback();
				}
			};
			//
			var validateChianiseName = function (rule, value, callback) {
				let reg_ChianiseName= /^[\u4E00-\u9FA5]{2,4}$/;
				if (value === '') {
					callback(new Error(''));
				}else if(!(reg_ChianiseName.test(value))){
					callback(new Error('2-4'));
				}else{
					callback();
				}
			};
			//
			var validateIDNumber = function (rule, value, callback) {
				let reg_IDNumber= /(^\d{15}$)|(^\d{17})(\d|X)$/;
				if (value === '') {
					callback(new Error('18'));
				}else if(!(reg_IDNumber.test(value))){
					callback(new Error('18'));
				}else{
					callback();
				}
			};

			//1
			var validatePass = (rule, value, callback) => {
				let reg_pass= /^(\w){6,16}$/;
				if (value === '') {
					callback(new Error(''));
				} else {
					if(!(reg_pass.test(value))){
						callback(new Error('6-16'));
					}else if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass');
					}
					callback();
				}
			};

			//
			var validatePass2 = (rule, value, callback) => {
				let reg_pass2= /^(\w){6,16}$/;
				if (value === '') {
					callback(new Error(''));
				}else if(!(reg_pass2.test(value))){
					callback(new Error('6-16'));
				}else if (value !== this.ruleForm.pass) {
					callback(new Error('!'));	
				} else {
					callback();
				}
			};
 
			//
			var validatePhone = function (rule, value, callback) {
				let reg_phone= /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/;
				if (value === '') {
					callback(new Error(''));
				}else if(!(reg_phone.test(value))){
					callback(new Error(''));
				}else{
					callback();
				}
			};
			//
			var validateEmail = function (rule, value, callback) {
				var reg_email=/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
				if (value === '') {
					callback(new Error(''));
				}else if(!(reg_email.test(value))){
					callback(new Error(''));
				}else{
					callback();
				}
			};
			return {
				ruleForm: {//
					account: '',
					chianise_name:'',
					sex:'',
					age:'',
					IDNumber:'',
					phone:'',
					email:'',
					marriage:'',
					pass:'',
					checkPass:'',
					hobbies:[],
					job_experience:'',
					intrduoce:'',
					open_resume:false,					
				},
				rules: {//
					account: [
					{ required: true, validator:validateAccount, trigger: 'blur' },
					],
					chianise_name:[
					{ required: true, validator:validateChianiseName, trigger: 'blur' },
					],
					sex:[
					{ required: true, message: '', trigger: 'change' },
					],
					age:[
					{ type: 'date',required: true, message: '', trigger: 'change' },
					],
					IDNumber:[
					{ required: true, validator: validateIDNumber, trigger: 'blur' },
					],
					phone:[
					{ required: true,validator: validatePhone, trigger: 'blur' }
					],
					email:[
					{ required: true,validator: validateEmail, trigger: 'blur' }
					],
					pass:[					
					{ required: true,validator: validatePass, trigger: 'blur' }
					],
					checkPass:[					
					{ required: true,validator: validatePass2, trigger: 'blur' }	
					],
					marriage:[
					{ required: true, message: '', trigger: 'change' },
					],
					hobbies:[
					{ type: 'array', required: true, message: '', trigger: 'change' }
					],
					intrduoce:[
					{ required: true, message: '', trigger: 'blur' },
					],
					job_experience:[
					{ required: true, message: '', trigger: 'blur' },
					],
				}
			};
		},
		methods: {
			submitForm(formName) {
				var _this = this;
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let UserMsg = {
							account: this.ruleForm.account,
							chianise_name: this.ruleForm.chianise_name,
							sex: this.ruleForm.sex,
							age: this.ruleForm.age,
							IDNumber:this.ruleForm.IDNumber,
							phone:this.ruleForm.phone,
							email:this.ruleForm.email,
							marriage:this.ruleForm.marriage,
							pass:this.ruleForm.pass,
							checkPass:this.ruleForm.pass,
							hobbies:this.ruleForm.hobbies,
							job_experience:this.ruleForm.job_experience,
							intrduoce:this.ruleForm.intrduoce,
							open_resume:this.ruleForm.open_resume,
						};
						alert(""+ _this.ruleForm.account);
						console.log(_this.ruleForm.account);
					} else {
						alert("");
						console.log('');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.box{
		padding-left: 5px;
		padding-right: 5px;
	}
</style>



